import {
  Box,
  Center,
  Flex,
  Icon,
  Image,
  Skeleton,
  Text,
  useBreakpointValue,
} from "@chakra-ui/react";
import { FC, MouseEventHandler, useRef, useState } from "react";
import Slider from "react-slick";
import iconsImages from "../../constants/icons-images";
import { ImCircleLeft, ImCircleRight } from "react-icons/im";
import { nanoid } from "nanoid";
import { useNavigate } from "react-router-dom";
import Images_Icons from "../../constants/icons-images";
import { useGetShopsQuery, useGetTrendingBrandsQuery } from "../../redux/rtkQuery/api";
import { BrandDataProps, CategoryDataProps } from "../../interfaces/responseTypes";

interface SliderCarouselProps {
  shopArray: any;
  carouselTitle?: string;
  carouselImage?: string;
  showIcon?: boolean;
  isLoaded?: boolean;
  preLink?: string;
  onClick?: MouseEventHandler<HTMLDivElement> | undefined;
  plusButtonVisible?: boolean;
  forStore?: boolean;
}

const ShopsSliderCarousel: FC<SliderCarouselProps> = ({
  shopArray,
  carouselTitle,
  carouselImage,
  showIcon,
  onClick,
  preLink,
  isLoaded,
  plusButtonVisible,
  forStore,
}) => {
  const [isDragging, setDragging] = useState<boolean>(false);
  const sliderRef = useRef<any>(null);
  // const [activeArrow, setActiveArrow] = useState<any>(0);
  let navigate = useNavigate();
  const slidesToShow = useBreakpointValue(
    {
      base: 2.6,
      sm: 4,
      md: 4,
      lg: 4.5,
      xl: 4,
    },
    { fallback: "md" }
  );

  // console.log(shopArray);

  const { data: brandsData, isLoading: trendingShopsLoading } = useGetShopsQuery(
    { page: forStore ? shopArray + 1 : 3, limit: 3 },
    { refetchOnMountOrArgChange: true }
  );

  // console.log(brandsData?.data?.brands);

  const settings = {
    // focusOnSelect: true,
    dots: false,
    infinite: true,
    speed: 500,
    // slidesToShow,
    slidesToScroll: 1,
    variableWidth: true,
  };
  const { root, desc } = useStyles();

  function ArrowLeft() {
    sliderRef?.current?.slickPrev();
  }

  function ArrowRight() {
    sliderRef?.current?.slickNext();
  }

  // const { data: trendingBrands, isLoading: trendingBrandsLoading } = useGetShopsQuery(
  //   { page: 1, limit: 8 },
  //   { refetchOnMountOrArgChange: true }
  // );
  function handleClick({ id, slug, brandName }: { id: string; slug: string; brandName: string }) {
    // console.log(id, slug, brandName);

    if (isDragging === true) {
      return;
    }
    navigate(`${preLink ? preLink : ""}${id}`, {
      state: { id, slug, brandName },
    });
  }

  // console.log(trendingBrands?.brands?.brands, "OOPOPOPasss");
  return (
    <Flex {...root} flexDir={"column"}>
      {/* @ts-ignore */}
      {brandsData?.data?.brands?.length > 0 && (
        <>
          <Flex w={"100%"} my="20px" justify={"space-between"}>
            <Flex
              flexDir={"row"}
              align="center"
              // w={["25%", "16%", "15%", "15%", "30%", "30%"]}
              w={"60%"}
            >
              {showIcon && (
                <Image
                  src={iconsImages.appLogo}
                  w={{ base: "40px", md: "50px" }}
                  h={{ base: "40px", md: "50px" }}
                  mr={"1%"}
                />
              )}
              {/* <Text {...desc}>{shopArray?}</Text> */}
            </Flex>
            <Flex display={{ base: "none", md: "flex" }} justify={"space-between"} w={"50px"}>
              {[
                { id: "1", icon: ImCircleLeft, fn: ArrowLeft },
                { id: "2", icon: ImCircleRight, fn: ArrowRight },
              ]?.map((e) => (
                <Box onClick={e.fn} key={nanoid()} as="button">
                  <Icon
                    as={e.icon}
                    w={"20px"}
                    h={"20px"}
                    color={"rgba(153,153,153,0.66)"}
                    _hover={{ color: "black" }}
                  />
                </Box>
              ))}
            </Flex>
          </Flex>
          <Box w={"100%"}>
            {/* pl={"5%"}> */}
            <Slider
              ref={sliderRef}
              {...settings}
              onSwipe={() => {
                setDragging(true);
                setTimeout(() => {
                  setDragging(false);
                }, 300);
              }}
            >
              {brandsData?.data?.brands?.map((e: any) => (
                <Box key={nanoid()}>
                  {/* @ts-ignore */}
                  <Skeleton
                    isLoaded={true}
                    mr={"20px"}
                    overflow={"hidden"}
                    w={["120px", "200px", "280px", "320px", "400px"]}
                    h={["100px", "180px", "180px", "180px", "200px"]}
                    onClick={() =>
                      handleClick({
                        id: e?.id,
                        slug: e?.slug,
                        brandName: e?.name,
                      })
                    }
                    role={"group"}
                  >
                    {/* {console.log(e?.cover_images)} */}

                    <Center
                      w={["120px", "200px", "280px", "320px", "400px"]}
                      h={["100px", "180px", "180px", "180px", "200px"]}
                      bg={"#0000003D"}
                      borderRadius={{ base: "10px", md: "16px" }}
                      pos="absolute"
                      background={`url(${e?.banners ? e?.banners[0] : ""}) center/cover no-repeat`}
                      backgroundColor="rgba(0, 0, 0, 0.5)"
                      backgroundBlendMode="multiply"
                      as="button"
                      overflow={"hidden"}
                      // backgroundColor="grey"
                    >
                      {/* plus Icon is here */}

                      {plusButtonVisible && (
                        <Image
                          src={Images_Icons.brandPlusIcon}
                          w={{ base: "20px", md: "30px" }}
                          h={{ base: "20px", md: "30px" }}
                          pos="absolute"
                          bottom="2"
                          right="2"
                        />
                      )}

                      {e?.logo_white && (
                        <Image
                          w={"130px"}
                          h={"130px"}
                          position="relative"
                          borderRadius={"5px"}
                          src={e?.logo_white}
                          objectFit={"contain"}
                          fallbackSrc={Images_Icons.appLogoPlaceholder}
                          fallbackStrategy={"beforeLoadOrError"}
                          _groupHover={{
                            transform: "scale(1.2)",
                            transition: "all .2s linear",
                          }}
                        />
                      )}
                    </Center>
                  </Skeleton>
                  <Text
                    w={["120px", "200px", "280px", "320px", "400px"]}
                    noOfLines={1}
                    mt={"12px"}
                    color="dark"
                    fontSize={["10px", "10px", "12px", "12px", "14px"]}
                  >
                    {e?.name}
                  </Text>
                </Box>
              ))}
            </Slider>
          </Box>{" "}
        </>
      )}
    </Flex>
  );
};

export default ShopsSliderCarousel;

const useStyles = () => {
  return {
    root: {
      w: "100%",
      // pt: "2vh",
      align: "center",
      // pb: "2%",
      overflow: "hidden",
    },
    desc: {
      // pb: "10px",
      fontSize: ["16px", "16px", "18px", "18px", "18px"],
      w: {
        base: "100%",
        md: "80%",
      },
    },
    seeMoreButton: {
      w: ["25%", "16%", "15%", "15%", "10%", "10%"],
      h: ["15px", "15px", "8%", "8%", "10%", "45px"],
      color: "dark",
      borderWidth: 1,
      backgroundColor: "light",
      mt: "2%",
      fontSize: ["12px", "14px", "14px", "14px", "14px", "18px"],
    },
  };
};
